},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center mb-4",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-7 col-md-12 col-sm-12"},[_c('p',{staticClass:"mb-5"},[_vm._v("El Plan de acción intersectorial de entornos saludables (PAIES) es un instrumento de gestión intersectorial que tiene como propósito fundamental orientar, articular y fortalecer los programas y acciones destinadas a mejorar las condiciones socioambientales y sanitarias de los diferentes entornos en los que las personas desarrollan su vida cotidiana, tales como la vivienda, la escuela y los lugares de trabajo. ")]),_c('div',{staticClass:"cajon color-primario p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("El plan de acción también busca no solo intervenir sobre los factores que afectan la salud y el bienestar de la población, sino también prevenir el deterioro de dichos entornos mediante un enfoque de promoción de la salud y prevención de riesgos, asimismo, el PAIES debe concebirse como un documento flexible y dinámico, que se ajuste a las realidades y particularidades de cada territorio, por lo cual requiere una evaluación y actualización periódica, de acuerdo con las necesidades emergentes y las transformaciones del contexto social, económico, ambiental y sanitario, de esta manera, se garantiza su pertinencia y efectividad en la implementación de políticas públicas integrales, centradas en la equidad, la participación social y la sostenibilidad.")])])]),_c('div',{staticClass:"col-lg-5 col-md-8 col-8 d-none d-lg-block"},[_c('img',{staticClass:"mt-2",attrs:{"src":require("@/assets/curso/temas/t2/22.png"),"alt":""}})])])
